<template>
  <div id="app">
    <!--Header-->
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01"
            aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarTogglerDemo01">
      <router-link :to="{ name: 'login' }" class="navbar-brand text-light">Time Manager</router-link>
      <ul class="navbar-nav mr-auto"></ul>

      <form class="form-inline my-2 my-lg-0">

        <li class="navbar-nav my-2" style="margin-left: 10px">
          <button class="btn btn-danger" v-on:click="deco" v-if="checkCo()">Disconnect</button>
        </li>
      </form>
    </div>
  </nav>
        <!---->


    <!--<router-link :to="{ name: 'users' }">users</router-link>-->
    <router-view></router-view>
  </div>
</template>

<script>
    import axios from 'axios';
    import router from './router/';
    import Header from "./components/Header";
    import auth from '@/services/auth';
    import login from '@/services/login'
    export default {
        name: 'App',
        components: {Header},
        name: 'Users',
        data() {
            return {
                msg: 'Loaad',
                list_user: "lala",
                q: "",
                new_user: {
                    username: "",
                    email: ""
                },
                error: ""
            }
        },created(){
          console.log(this.$router.history.current)
          if (auth.token == null){
            if (this.$router.history.current.path != "/login" && this.$router.history.current.path != "/NewUser")
            {
              console.log("je passe ici")            
              this.$router.push("/login")}

          }
        },
        updated(){
          if (auth.token == null){
            if (this.$router.history.current.path != "/login" && this.$router.history.current.path != "/NewUser")
             {
              console.log("je passe la")            
              this.$router.push("/login")}
          }
        },
        methods: {
            checkCo(){
              console.log(auth.isAuth())
              return auth.isAuth();
            },
            navigate(id) {
                router.push({name: "user", params: {id: id}})
            },
              deco(){
            login.logOut()
          }
        }
    }
</script>

<style>
  @media screen and (min-width: 640px) {
    #app {
      height: 100%;
      color: #2c3e50;
    }

    .navbar {
      z-index: 5000;
      position: fixed;
      top: 0;
      width: 100%;
    }
  }

  @media screen and (max-width: 990px) {
    #app {
      color: #2c3e50;
    }

    .navbar{
      z-index: 5000;
      top: 0;
      width: 100%;
    }
  }
</style>



